<template>

   <div>

    <button @click="block">{{data.title}}</button>
    
    <div :id="id" class="modal">
    
      <span @click="hide" class="close" title="Fechar">×</span>
      
      <form class="modal-content">
      
        <div class="container">
          
          <h1>{{data.title}}</h1>
          <p>{{data.msg}}</p>
          
          <div class="clearfix">
              <button type="button" @click="hide" class="cancelbtn">Cancelar</button>
              <button type="button" @click="ex" class="confirmbtn">{{data.confirmOption}}</button>
          </div>
      
        </div>
      
      </form>
    
    </div>

   </div>
    
        
    </template>
    
    <script>
    
    import { bookStore } from '@/store/books';
    
    export default {
    
        name: "Confirmation",
    
        props:{
    
            data:  Object
    
        },

        data(){

          return{

            id:null

          }


        },

      
    
        mounted(){

          this.id = ++bookStore().deleteCounter
          
          // Get the modal
          let modal = document.getElementById(this.id);
  
          // When the user clicks anywhere outside of the modal, close it
          window.onclick = function(event) { 
            if (event.target == modal) { alert("funciona")
                modal.style.display = "none";
            }
          }
            
        },


        methods:{

          ex(){

            this.$emit('confirm')
            this.hide()

          },

          hide(){
            document.getElementById(this.id).style.display='none'
          },

          block(){
            document.getElementById(this.id).style.display='block'
          }

        }
    
    }
    
    
    </script>
    
    <style scoped>
    
    /* Set a style for all buttons */
    button {
      background-color: #ee5959;
      color: white;
      padding: 14px 20px;
      margin: 8px 0;
      border: none;
      cursor: pointer;
      opacity: 0.9;
      width: 300px;
    }
    
    button:hover {
      opacity:1;
    }
    
    /* Float cancel and delete buttons and add an equal width */
    .cancelbtn, .confirmbtn {
      float: left;
      width: 50%;
    }
    
    /* Add a color to the cancel button */
    .cancelbtn {
      background-color: #ccc;
      color: black;
    }
    
    /* Add a color to the delete button */
    .deletebtn {
      background-color: #f44336;
    }
    
    /* Add padding and center-align text to the container */
    .container {
      padding: 16px;
      text-align: center;
    }
    
    /* The Modal (background) */
    .modal {
      display: none; /* Hidden by default */
      position: fixed; /* Stay in place */
      z-index: 1; /* Sit on top */
      left: 0;
      top: 0;
      width: 100%; /* Full width */
      height: 100%; /* Full height */
      overflow: auto; /* Enable scroll if needed */
      background-color: #474e5d;
      padding-top: 50px;
    }
    
    /* Modal Content/Box */
    .modal-content {
      background-color: #fefefe;
      margin: 5% auto 15% auto; /* 5% from the top, 15% from the bottom and centered */
      border: 1px solid #888;
      width: 80%; /* Could be more or less, depending on screen size */
    }
    
    /* Style the horizontal ruler */
    hr {
      border: 1px solid #f1f1f1;
      margin-bottom: 25px;
    }
     
    /* The Modal Close Button (x) */
    .close {
      position: absolute;
      right: 35px;
      top: 15px;
      font-size: 40px;
      font-weight: bold;
      color: #f1f1f1;
    }
    
    .close:hover,
    .close:focus {
      color: #f44336;
      cursor: pointer;
    }
    
    /* Clear floats */
    .clearfix::after {
      content: "";
      clear: both;
      display: table;
    }
    
    /* Change styles for cancel button and delete button on extra small screens */
    @media screen and (max-width: 300px) {
      .cancelbtn, .deletebtn {
         width: 100%;
      }
    }
    </style>